import React, { useState, useEffect, useRef } from "react";
import {
  Button,
  CircularProgress,
  Stack,
  Stepper,
  Step,
  StepLabel,
} from "@material-ui/core";
import { styled } from "@material-ui/styles";
import { useNavigate } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { Form, Formik, FormikHelpers } from "formik";
import { Box } from "@material-ui/system";
import { useMutation, UseMutationResult } from "react-query";

import {
  AddFunction,
  CreateProfileResponse,
  CreateProfileTypeDonor,
  ILoginResponse,
  RootAppState,
} from "../../../../utils/typeHelpers";
import { CreateProfileSchemaDonor } from "../../../../utils/formSchemas";
import { CREATE_PROFILEDONOR_API } from "../../../../utils/Api";
import { ErrorBoundary } from "react-error-boundary";
import PageErrorBoundary from "../../../Errror/PageErrorBoundary";
import { saveUserInfoAction } from "../../../../redux/actions/saveUserInfoAction";
import { createProfileInitialValueDonor } from "../../../../utils/initialvalues";

import AlertNotification from "../../Components/AlertNotification";
import DonarInfoForm from "../../Components/DonarInfoForm";
import { sleepTimer } from "../../../../utils/helperFunction";
import { MessageError } from "../../../../components/AlertMessageError";
import { MessageSuccess } from "../../../../components/AlertMessageSuccess";
import { saveLoginAction } from "../../../../redux/actions/loginAction";

const CreateProfile = () => {
  const refError = useRef<null | AddFunction>(null);
  const refSuccess = useRef<null | AddFunction>(null);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const userInfo = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo
  );

  const ngo_profile_id = useSelector(
    (state: RootAppState) => state.AuthReducer.ngo_profile_id
  );

  //api mutation
  const mutationCreate: UseMutationResult<
    CreateProfileResponse,
    Error,
    CreateProfileTypeDonor
  > = useMutation((infoProfile: CreateProfileTypeDonor) =>
    CREATE_PROFILEDONOR_API(infoProfile, userInfo.access)
  );

  //variables
  const { isError, error, isSuccess, data } = mutationCreate;

  const steps = ["Basic Info", "Notifications"];
  let [activeStep, setActiveStep] = useState(0);
  const [explode, setExplode] = useState(false);
  const label = "Create Profile Donor";

  const [initialValuesProfile, setInitialValuesProfile] =
    useState<CreateProfileTypeDonor>({
      ...createProfileInitialValueDonor,
    });

  const selectedValidationSchema = CreateProfileSchemaDonor[activeStep];
  const isLast = activeStep === steps.length - 1;

  //submitform
  const submitForm = async (
    values: CreateProfileTypeDonor,
    actions: FormikHelpers<CreateProfileTypeDonor>
  ) => {
    await sleepTimer(200);

    mutationCreate.mutate(values);
    actions.setSubmitting(false);
  };

  //change step and validate fields of form
  const handleSubmit = (
    values: CreateProfileTypeDonor,
    actions: FormikHelpers<CreateProfileTypeDonor>
  ) => {
    if (isLast) {
      submitForm(values, actions);
    } else {
      setActiveStep((prev) => prev + 1);
      actions.setTouched({});
      actions.setSubmitting(false);
    }
  };

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleCreateSucess = (msg: string) => {
    refSuccess.current?.(msg);
  };

  useEffect(() => {
    if (isSuccess) {
      if (data !== undefined) {
        if (typeof data.full_name === "string") {
          handleCreateSucess("Profile Create Succesfull");

          const userInfoData: ILoginResponse = {
            refresh: userInfo.refresh,
            access: userInfo.access,
            is_account_created: true,
            user_profile: null,
            user_id: userInfo.user_id,
          };

          setTimeout(() => {
            if (ngo_profile_id !== "") {
              dispatch(saveUserInfoAction(userInfoData));
              dispatch(saveLoginAction(true));
              navigate("../createprofiledonee");
            } else {
              dispatch(saveUserInfoAction(userInfoData));
              dispatch(saveLoginAction(true));
              navigate("../", { replace: true });
            }
          }, 5000);
        } else {
          const datas = data as any;
          const msg: any = datas[0];
          handleCreateError(msg);
        }
      }
    } else if (isError) {
      throw new Error(error?.message);
    }
  }, [isError, isSuccess]);

  return (
    <ErrorBoundary
      fallbackRender={({ error, resetErrorBoundary }) => (
        <PageErrorBoundary
          error={error}
          resetErrorBoundary={resetErrorBoundary}
        />
      )}
      onReset={() => setExplode(false)}
      resetKeys={[explode]}
    >
      <RootContainerStyle>
        <MessageError
          children={(add: AddFunction) => {
            refError.current = add;
          }}
        />
        <MessageSuccess
          children={(add: AddFunction) => {
            refSuccess.current = add;
          }}
        />
        <Stepper activeStep={activeStep}>
          {steps.map((step, index) => {
            return (
              <Step key={step}>
                <StepLabel>{step}</StepLabel>
              </Step>
            );
          })}
        </Stepper>
        <Formik
          enableReinitialize
          initialValues={initialValuesProfile}
          validationSchema={selectedValidationSchema}
          onSubmit={handleSubmit}
        >
          {(formikProps) => (
            <Form>
              <Stack spacing={2}>
                {renderStepContent(activeStep, label)}
                <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                  {activeStep > 0 && (
                    <Button onClick={() => setActiveStep((prev) => prev - 1)}>
                      Back
                    </Button>
                  )}

                  <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                    disabled={formikProps.isSubmitting}
                  >
                    {isLast ? "Create Profile" : "Next"}
                  </Button>
                  {formikProps.isSubmitting && <CircularProgress />}
                </Box>
              </Stack>
            </Form>
          )}
        </Formik>
      </RootContainerStyle>
    </ErrorBoundary>
  );
};

export default CreateProfile;

export const renderStepContent = (step: number, label: string) => {
  switch (step) {
    case 0:
      return <DonarInfoForm label={label} />;

    case 1:
      return <AlertNotification />;
  }
};

const RootContainerStyle = styled("div")({
  padding: 10,
});
