import { Typography, Container } from "@material-ui/core";
import { Field } from "formik";
import React from "react";
import { useQuery, UseQueryResult } from "react-query";
import { useSelector } from "react-redux";
// import { GET_COUNTRIES_API } from "../../../utils/Api";
import { SdgsType, inputType, RootAppState } from "../../../utils/typeHelpers";
import { textInputsInfoDonor } from "../utils/data";

import BioInputFieldProfile from "./BioInputField";
import ImageField from "./ImageField";
import InputFieldProfile from "./InputFieldProfile";
import SelectFieldCountry from "./SelectFieldCountry";
import useAxios from "../../../utils/useAxios";
import Loader from "../../../components/Loader";
import ErrorCatchComponent from "../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../Errror/ApiErrorPage";

interface DonorFormType {
  label: string;
}

const DonarInfoForm = ({ label }: DonorFormType) => {
  const accesstoken = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.access
  );

  const axiosInstance = useAxios();
  const {
    data: options,
    isLoading,
    isError,
    error,
  }: UseQueryResult<SdgsType[], Error> = useQuery(
    ["getCountries", accesstoken],
    async () => {
      const response = await axiosInstance.get(`/country-list/`);

      return response.data;
    }
  );

  return (
    <ErrorCatchComponent>
      {isLoading === false ? (
        <div>
          {isError ? (
            <ApiErrorPage
              error={error ? error : new Error("Network response was not ok.")}
            />
          ) : (
            <Container maxWidth="sm">
              <Typography variant="h4" gutterBottom>
                {label}
              </Typography>
              <Field name="image" component={ImageField} type="input" />

              {options !== undefined &&
                textInputsInfoDonor.map((item: inputType) =>
                  item.label === "Location" ? (
                    <div style={{ margin: 5 }}>
                      {options && (
                        <Field
                          name={item.name}
                          options={options}
                          component={SelectFieldCountry}
                          placeholder="Select your country"
                          isMulti={false}
                        />
                      )}
                    </div>
                  ) : (
                    <div style={{ margin: 5 }}>
                      <Field
                        name={item.name}
                        component={InputFieldProfile}
                        label={item.label}
                        placeholder={item.placeholder}
                        type={item.placeholder}
                      />
                    </div>
                  )
                )}
              <div style={{ margin: 5 }}>
                <Field
                  name="bio"
                  component={BioInputFieldProfile}
                  label="Bio"
                  placeholder="Enter Your Info"
                />
              </div>
            </Container>
          )}
        </div>
      ) : (
        <Loader />
      )}
    </ErrorCatchComponent>
  );
};

export default DonarInfoForm;
